'use client';
import { useState, useEffect } from "react";
import { Menu, X } from "lucide-react";
import ThemeToggle from "./components/ThemeToggle";

interface NavbarProps {
  activeSection: string;
}

const navItems = [
  { label: "Accueil", href: "#accueil" },
  { label: "√Ä propos", href: "#apropos" },
  { label: "Projets", href: "#projets" },
  { label: "Comp√©tences", href: "#competances" },
  { label: "Contact", href: "#contact" },
];

export default function Navbar({ activeSection }: NavbarProps) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  // D√©tecter la taille de l'√©cran
  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <nav
      style={{
        position: "fixed",
        top: 0,
        width: "100%",
        
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        padding: "10px 20px",
        zIndex: 1000,
      }}
    >
      {/* Nom */}
      <div
        style={{
          fontWeight: "bold",
          fontSize: "1.2rem",
          color: "var(--text-color)",
          paddingLeft: "20px",
        }}
      >
        HASINIRINA Jean de Dieu
      </div>

      {/* Liens + Toggle */}
      {!isMobile ? (
        <div style={{ display: "flex", alignItems: "center", gap: "20px", paddingRight: "20px" }}>
          {navItems.map((item) => {
            const sectionId = item.href.replace("#", "");
            return (
              <a
                key={item.href}
                href={item.href}
                style={{
                  color: activeSection === sectionId ? "#17f" : "var(--text-color)",
                  fontWeight: activeSection === sectionId ? "bold" : "normal",
                  textDecoration: "none",
                  transition: "0.3s",
                  cursor: "pointer",
                  padding: "6px 10px",
                  borderRadius: "6px",
                  fontFamily: "cursive",
                }}
              >
                {item.label}
              </a>
            );
          })}
          <ThemeToggle />
        </div>
      ) : (
        <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
          <ThemeToggle />
          <button
            onClick={() => setMenuOpen(!menuOpen)}
            style={{ background: "none", border: "none", cursor: "pointer" }}
          >
            {menuOpen ? <X color="#fff" size={28} /> : <Menu color="#fff" size={28} />}
          </button>
        </div>
      )}

      {/* Menu mobile d√©roulant */}
      {menuOpen && isMobile && (
        <div
          style={{
            position: "absolute",
            top: "100%",
            right: 0,
            backgroundColor: "var(--btn-bg-color)",
            display: "flex",
            flexDirection: "column",
            padding: "15px",
            gap: "10px",
          }}
        >
          {navItems.map((item) => {
            const sectionId = item.href.replace("#", "");
            return (
              <a
                key={item.href}
                href={item.href}
                onClick={() => setMenuOpen(false)}
                style={{
                  color: activeSection === sectionId ? "#17f" : "var(--text-color)",
                  fontWeight: activeSection === sectionId ? "bold" : "normal",
                  textDecoration: "none",
                  padding: "8px 12px",
                  borderRadius: "6px",
                  cursor: "pointer",
                }}
              >
                {item.label}
              </a>
            );
          })}
        </div>
      )}
    </nav>
  );
}




